<template>
  <v-app id="app">
    <transition mode="out-in" :enter-active-class="transitionIn">
      <keep-alive>
        <router-view v-if="$route.meta.keepAlive"></router-view>
      </keep-alive>
    </transition>
    <transition mode="out-in" :enter-active-class="transitionIn">
      <router-view v-if="!$route.meta.keepAlive"></router-view>
    </transition>

    <!-- <transition :name="transitionName">
      <router-view></router-view>
    </transition>-->
    <v-snackbar
      v-model="$store.state.snackbar.show"
      :color="$store.state.snackbar.color"
      :timeout="$store.state.snackbar.time"
      top
    >{{$store.state.snackbar.message}}</v-snackbar>
  </v-app>
</template>

<script>
import store from "@/store/store";
import Authorization from '@/utils/Authorization'
export default {
  name: "App",
  data() {
    return {
      transitionIn: "",
      transitionOut: "",
      transitionName: "",
      snackbar: {},
      exitCnt: 0,
    };
  },
  created() {
    let that = this;
    document.addEventListener("backbutton", function (e) {
      if ((location.hash == '#/main/hot' || location.hash == '#/main/index' || location.hash == '#/main/evaluation') && !that.$store.state.dialog) {
        that.showSnackbar("再点一次退出程序", "error", 1500);
        that.exitCnt++;
        setTimeout(() => {
          that.exitCnt--;
        }, 1500);
      } else if (

        that.$store.state.keyboardShow
      ) {
        cordova.plugins.Keyboard.close();
        that.$store.change_keyboard(false);
      } else if (that.$store.state.drawer) {
        that.$store.change_drawer(false);
      }
      else if (that.$store.state.dialog) {
        if (that.$store.state.dialogName != 'voteDialog.show')
          that.$store.change_dialog(false);
      }
      else {
        history.go(-1);
      }
    });   

  },
  watch: {
    //使用watch 监听$router的变化
    $route(to, from) {
      console.log(from);
      //如果to索引大于from索引,判断为前进状态,反之则为后退状态
      if (to.meta.index > from.meta.index) {
        //设置动画名称
        this.transitionIn = "animate__animated animate__fadeInRight";
        this.transitionOut = "animate__animated animate__fadeOut";
      } else if (to.meta.index < from.meta.index) {
        this.transitionIn = "animate__animated animate__fadeIn";
        this.transitionOut = "";
      } else {
        this.transitionIn = "animate__animated animate__fadeIn";
      }
    },
    exitCnt(v) {
      if (v == 2) navigator.app.exitApp();
    },
    "store.state.snackbar"(v) {
      if (v.show) {
        this.snackbar = v;
      }
    },
  },
};
</script>

<style>
#app {
  font-family: "FZSSJW";
}
.animate__animated.animate__fadeInRight {
  --animate-duration: 500ms;
}
.animate__animated .animate__fadeOut {
  --animate-duration: 100ms;
}
.animate__animated .animate__fadeIn {
  --animate-duration: 200ms;
}
</style>



